import { Controller } from '@ali/4ever-cangjie';
import { Marks } from '@ali/4ever-mo';
import { addMarkAtCurrentRange }  from '@ali/4ever-utils';

export default function setSize(controller: Controller, size) {
  const mark = Marks.Sz.create({
    value: size,
    szUnit: 'pt',
  });
  return addMarkAtCurrentRange(controller, mark);
}
